import React, { useState } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import './App.css'; // CSS 파일 임포트
import Modal from 'react-modal';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const GraphView = ({ showDatasets, toggleDataset, chartData, chartOptions }) => (
    <div className="graph-container">
      <h2>Car Production and Average Temperature Data (1983-1990)</h2>
      <Line data={chartData} options={chartOptions} />
      <div className="button-container">
        {Object.keys(showDatasets).map((key) => (
            <button
                key={key}
                onClick={() => toggleDataset(key)}
                className="toggle-button"
                disabled={key === 'temperature'} // 온도 데이터 버튼 비활성화
            >
              Toggle {key === 'temperature' ? 'Average Temperature' : key}
            </button>
        ))}
      </div>
      <div className="description">
        <p>
          [Brief Description of the Graph]
          <br />
          The Above Graph shows the data of JDM sports cars' production volume per year, and the average temperature of the island Japan.
          By clicking the toggle buttons, you are able to show and hide different data.
          <br />
          <br />
          [Interesting Point]
          <br />
          Along with the sports cars, I have added the 'Honda Civic' model's data, which is by far the most popular JDM model, although it is not a sports car.
          By showing and hiding the 'Honda Civic' data, you may be able to see how the popular model affected the environment.
          <br />
          <br />
          [Interpretation]
          <br />
          Although the sports cars that were produced in Japan might have affected the environment, their impact was no larger than the effect of a one single popular model
          that was sold great globally.
        </p>
      </div>
    </div>
);

const ImageView = () => {
  const carModels = [
    'Toyota AE86',
    'Honda CR-X',
    'Toyota Supra',
    'Mazda RX-7',
    'Toyota MR2',
    'Nissan Fairlady',
    'Honda Prelude',
    'Mitsubishi Starion',
    'Toyota Celica GT-Four',
    'Honda Civic',
  ];

  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState('');

  const openModal = (image) => {
    setSelectedImage(image);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setSelectedImage('');
  };

  return (
      <div className="image-container">
        <h2>Image Gallery</h2>
        <div className="gallery">
          {carModels.map((model, index) => (
              <div className="gallery-item" key={index}>
                <img
                    src={`images/${model.replace(' ', '_').toLowerCase()}.jpg`}
                    alt={model}
                    onClick={() => openModal(`images/${model.replace(' ', '_').toLowerCase()}.jpg`)}
                />
                <p>{model}</p>
              </div>
          ))}
        </div>
        <Modal isOpen={modalIsOpen} onRequestClose={closeModal} className="modal" overlayClassName="overlay">
          <img src={selectedImage} alt="Expanded view" className="modal-image" />
          <button onClick={closeModal} className="modal-close-button">Close</button>
        </Modal>
      </div>
  );
};

